<template>
  <div>
    <div class="h1 cursor-pointer"></div>
    <p class="cursor-pointer"></p>
    <span
      class="btn option-badge mt-4 mb-3"
      @click="openPopupMap"
      v-if="!location.city_on_map"
    >
      Ajouter une adresse
    </span>
    <div class="h1 cursor-pointer" @click="openPopupMap">
      {{ location.city_on_map }}
    </div>
    <p class="cursor-pointer">{{ location.current_address }}</p>
    <AppPopUpMap
      ref="apppopupmap"
      :etape-checkout="false"
      :action-after="false"
      @update_location="update_location"
    ></AppPopUpMap>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "MapGoogle",
  components: {
    AppPopUpMap: () => import("map-google-location/src/AppPopUpMap.vue")
  },
  computed: {
    ...mapState(["location"])
  },
  methods: {
    openPopupMap() {
      this.$refs.apppopupmap.openModal();
    },
    update_location(location) {
      this.$store.dispatch("setLocation", location);
    }
  }
};
</script>
